.footer {
    background-color: #012323;
    margin-bottom: 0 !important;
    margin-left: 0 !important;
    margin-right: 0 !important;
    padding: 0;
    bottom: 0;
}

.row {
    margin-left: 0px !important;
    margin-right: 0px !important;
}

.foot-pad {
    padding: calc(5.55vw + 0.08px) calc(3.33vw + 0.048px) !important;
}

.mt80 {
    margin-top: 80px !important;
}

.no-underline {
    text-decoration: none;
}

.link-underline {
    text-decoration: none;
}

.link-underline:hover {
    text-decoration: underline;
    text-decoration-color: whitesmoke !important;
}

.mt-43 {
    margin-top: 0px;
    margin-bottom: calc(1.11vw + 0.016px);
}

.p-64 {
    padding-top: calc(2.91vw + 0.096px);
    padding-bottom: calc(5.55vw + 0.08px);
}


.col,
.col-1,
.col-10,
.col-11,
.col-12,
.col-2,
.col-3,
.col-4,
.col-5,
.col-6,
.col-7,
.col-8,
.col-9,
.col-auto,
.col-lg,
.col-lg-1,
.col-lg-10,
.col-lg-11,
.col-lg-12,
.col-lg-2,
.col-lg-3,
.col-lg-4,
.col-lg-5,
.col-lg-6,
.col-lg-7,
.col-lg-8,
.col-lg-9,
.col-lg-auto,
.col-md,
.col-md-1,
.col-md-10,
.col-md-11,
.col-md-12,
.col-md-2,
.col-md-3,
.col-md-4,
.col-md-5,
.col-md-6,
.col-md-7,
.col-md-8,
.col-md-9,
.col-md-auto,
.col-sm,
.col-sm-1,
.col-sm-10,
.col-sm-11,
.col-sm-12,
.col-sm-2,
.col-sm-3,
.col-sm-4,
.col-sm-5,
.col-sm-6,
.col-sm-7,
.col-sm-8,
.col-sm-9,
.col-sm-auto,
.col-xl,
.col-xl-1,
.col-xl-10,
.col-xl-11,
.col-xl-12,
.col-xl-2,
.col-xl-3,
.col-xl-4,
.col-xl-5,
.col-xl-6,
.col-xl-7,
.col-xl-8,
.col-xl-9,
.col-xl-auto {
    padding-right: 0px !important;
    padding-left: 0px !important;
}

.fsr-16-14 {
    font-size: 16px;
    font-weight: 400;
}

@media only screen and (max-width: 767px) {
    .pr-8 {
        padding-right: calc(5.41vw + 0.0108px) !important;
    }

    .mt-43 {
        margin-top: calc(7.76vw + 0.0288px);
        margin-bottom: calc(3.88vw + 0.0144px);
    }

    .fsr-16 {
        font-size: 12px !important;
        font-weight: 400;
    }

    .fsr-21 {
        font-size: 16px !important;
        font-weight: 400;
    }

    .color-lightwhite {
        color: #BFC0C0 !important;
    }

    .p-64 {
        padding-top: calc(10.19vw + 0.0172px);
        padding-bottom: calc(15.53vw + 0.0164px);
    }

    .foot-pad {
        padding: calc(15.53vw + 0.0164px) calc(3.88vw + 0.0144px) !important;
    }

    .mt80 {
        margin-top: 80px !important;
    }

    .fsr-16-14 {
        font-size: 14px !important;
        font-weight: 400;
    }

}

@media only screen and (min-width: 1600px) {
    .foot-pad {
        padding: 80px 48px !important;
    }

    .section-wrap {
        padding-left: 90px;
        padding-left: calc((100vw - 1600px) / 2);
        padding-right: calc((100vw - 1600px) / 2);
        background-color: #012323;
    }
}